body,
html,
ul,
li,
button,
input,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: "Muli", sans-serif;
  margin: 0;
  padding: 0;
  color: #253757;
}

ul {
  list-style: none;
}

a,
a:visited {
  text-decoration: none;
}

body {
  background: #fcfcfd;
}

body,
html {
  overflow-x: hidden;
}

.dia {
  display: block;
}
