.header {
  &__name {
    font-weight: 900;
    font-size: 64px;
    line-height: 150%;
    color: $main-color;
    margin-bottom: 22px;
    margin-top: 80px;
  }
  &__descrip {
    font-size: 24px;
    line-height: 150%;
    color: $main-color;
  }
  &__button {
    margin-top: 42px;
    margin-bottom: 80px;
    font-weight: bold;
    font-size: 16px;
    line-height: 150%;
    background-color: $main-color;
    border-radius: 64px;
    width: 226px;
    height: 56px;
    border: none;
    cursor: pointer;
    &:hover {
      background-color: #e1e1f3;
    }
  }
}

@media screen and (max-width: 450px) {
  .header__button {
    width: 100px;
    height: 56px;
  }
  .header {
    background: #56ccf2;
    height: 800px;
  }
}

@media screen and (max-width: 698px) {
  .header__button {
    width: 200px;
    height: 56px;
  }
  .header {
    background: #56ccf2;

  }
  .menu__toggle {
    opacity: 0;
    position: absolute;
    top: -100%;
  }

  .menu__toggle:checked ~ .menu__btn > .menu__span {
    transform: rotate(45deg);
  }

  .menu__toggle:checked ~ .menu__btn > .menu__span::before {
    top: 0;
    transform: rotate(0);
  }

  .menu__toggle:checked ~ .menu__btn > .menu__span::after {
    top: 0;
    transform: rotate(90deg);
  }

  .menu__toggle:checked ~ .nav__list {
    opacity: 1;
    top: 40px;
  }

  .menu__btn {
    display: block;
    position: relative;
    margin: 20px auto 0;
    width: 40px;
    height: 100%;
    cursor: pointer;
    z-index: 1;
  }

  .menu__toggle:checked ~ .menu__btn >.menu__span {
  &::before,
  &::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 4px;
    background-color: #253757;
    transition-duration: 0.3s;
  }
}
.nav__item_logo{
  display: none;
}
  .menu__span::before {
    content: '';
    top: -8px;
  }

  .menu__span::after {
    content: '';
    top: 8px;
  }

  .header__nav {
    display: flex;
    flex-direction: column;
    justify-items: center;
    position: relative;
  }

  .nav__list {
    display: flex;
    text-align: center;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    position: absolute;
    opacity: 0;
    margin: 0 auto;
    top: -100%;
    left: 50%;
    transform: translate(-50%, 0);
    width: 90%;
    padding: 70px 0;
    list-style: none;
    background-color: #fff;
    border-radius: 20px;
    box-shadow: 2px 0px 6px rgba(0, 0, 0, .4);
    transition: opacity 2s, top 1s, background-color 0.3s, color 0.3s;
  }

  .nav__link {
    display: block;
    margin-left: 30px;
    color: #56ccf2;
    border-radius: 15px;
    font-family: 'Roboto', sans-serif;
    font-size: 24px;
    font-weight: 600;
    text-decoration: none;
    transition-duration: .1s;
    &_active {
      &::after {
        width: 80px;
      }
    }
  }

  .nav__link:hover {
    color: #fcc304;

  }

  .nav__link:active {
    color: #fcc304;
  }
}
