.footer {
  display: flex;
  margin-top: 20px;
  margin-bottom: 83px;
  justify-content: center;
  &__design {
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 150%;
    margin-right: 320px;
    color: #727275;
  }
}

.navigation {

  &__list {
    display: flex;
  }
  &__item {
    padding: 0 32px;
  }

  &__link {
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 150%;
    color: #253757;
    &:hover {
      color: #727275;
    }
  }
}

@media screen and (max-width: 999px) {
  .footer {
    flex-direction: column-reverse;
    justify-content: center;
    margin-top: 0;
    &__design {
      margin-right: 0;
      margin-left: 12px;
      font-size: 14px;
    }

  }
  .navigation {
    width: 100%;
    display: flex;
    &__list {
      flex-wrap: wrap;
      justify-items: center;
    }
    &__item {
      justify-items: center;
      flex-direction: row;
    }
  }
}
